import uniCountdown from '@/components/uni-countdown/uni-countdown.vue'
export default {
	components: {uniCountdown},
	data() {
		return {
			goodsData:{},
			// 商品是否被收藏
			isCollect:false
		}
	},
	onLoad: function (e) {
	  var ids = e.id;
		uni.showToast({
			title: "编号" + ids,
			icon: "none"
		});
	  //获得当前对象
	  var that = this
		uni.request({
			url: 'http://localhost:8080/commodity/getById?id='+ids,
			method: 'POST',
			success: res => {
				this.goodsData=res.data;
				console.log(res.data)
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		//轮播图指示器
		// swiperChange(event) {
		// 	this.currentSwiper = event.detail.current;
		// },
		//规格弹窗
		showSpec(fun) {
			console.log('show');
			this.specClass = 'show';
			this.specCallback = fun;
		},
		// 首页
		toChat(){
			uni.navigateTo({
				url:""
			})
		},
		// 客服
		chat() {
			uni.reLaunch({
				url:'/pages/home/chat/chat'
			})
		},
		//购物车
		keep(){
			uni.navigateTo({
				url:""
			})
		},
		// 加入购物车
		joinCart(){
			if(this.selectSpec==null){
				return this.showSpec(()=>{
					uni.showToast({title: "已加入购物车"});
				});
			}
			uni.showToast({title: "已加入购物车"});
		},
		//点击加入购物车
		handleCartAdd() {
		    // 1 获取缓存中的购物车 数组
		    let cart = uni.getStorageSync("cart") || [];
		    // 2 判断 商品对象是否存在于购物车数组中
		    let index = cart.findIndex(v => v.goods_id === this.GoodsInfo.goods_id);
		    if (index === -1) {
		      //3  不存在 第一次添加
		      this.GoodsInfo.num = 1;
		      this.GoodsInfo.checked = true;
		      cart.push(this.GoodsInfo);
		    } else {
		      // 4 已经存在购物车数据 执行 num++
		      cart[index].num++;
		    }
		    // 5 把购物车重新添加回缓存中
		    uni.setStorageSync("cart", cart);
		    // 6 弹窗提示
		    uni.showToast({
		      title: '加入成功',
		      icon: 'success',
		      // true 防止用户 手抖 疯狂点击按钮 
		      mask: true
		    });
		},
		handleCollect() {
			console.log(this.goodsData);
			// 1 获取缓存中的商品收藏数组
			let collect = uni.getStorageSync("collect") || [];
			// 2 判断该商品是否被收藏过
			let index = collect.findIndex(v => v.id === this.goodsData.id);
			// 3 当index！=-1表示 已经收藏过 
			if (index !== -1) {
				// 能找到 已经收藏过了  在数组中删除该商品
				collect.splice(index, 1);
				this.isCollect = false;
				uni.showToast({
					title: '取消成功',
					icon: 'success',
					mask: true
				});
		
			} else {
				// 没有收藏过
				collect.push(this.goodsData);
				this.isCollect = true;
				uni.showToast({
					title: '收藏成功',
					icon: 'success',
					mask: true
				});
			}
			// 4 把数组存入到缓存中
			uni.setStorageSync("collect", collect);
		},
		//立即购买
		buy(){
			if(this.selectSpec==null){
				return this.showSpec(()=>{
					this.toConfirmation();
				});
			}
			this.toConfirmation();
		}
	}
}
